.App {
  text-align: center;
}

.streamInfo {
  display: flex;
  justify-content: space-between;
}

body, #page {
  background-color: #121212;
}

#nav {
  display: flex;
  justify-content: center;
  text-align: center;
  max-width: 900px;
  background-color: #ce93d8;
  color: white;
  font-size: 15px;
  font-weight: 400;
  height: 64px;
  line-height: 64px;
  position: relative;
}

nav {
  background-color: #121212;
}

.title {
  font-size: 2rem;
}

.playing img {
  max-width: 100%;
}

.live-channel {
  background-color: #1C1C1C;
  display: flex;
  padding: 1em;
  margin-bottom: 1em;
  margin-top: 1em;
  justify-content: center;
  max-width: 900px;
}

.top {
  max-width: 900px;
}

.channel {
  background-color: #1C1C1C;
}

.channel-info {
  width: 30%;
  padding-right: 1em;
}

.channel-description {
  width: 70%;
}

.streamer {
  width: 50%;
  display: flex;
  flex-direction:  column;
  justify-content: space-between;
  padding-right: 1em;
  border-right: 1px solid #e0e0e0;
}

.playing {
  width: 50%;
  display: flex;
  padding-left: 1em;
  flex-direction:  column;
  justify-content: space-between;
}

.profile-pic {
  width: 100px;
}

.description {
  background-color: #222;
  text-align: left;
}

strong {
  color: #a44;
}

button, button:active {
  background-color: transparent;
  border: none;
  cursor: pointer;
  text-decoration: underline;
  display: inline;
  margin: 0;
  padding: 0.1em;
  color: #039be5;
}

button:enabled {
  background-color: transparent;
}

.buttonEnabled {
  color: #f44336;
  background-color: transparent;
}


.top {
  justify-content: center;
  display: flex;
  color: #e0e0e0;
  width: 100%;
  background-color: #222;
}

.filterBy {
  padding-right: 5em;
}

.sortValues {
  align-content: space-between;
}

.sortValue {
  display: flex;
}

.sortButton {
  flex-grow: 2;
  display: flex;
  justify-content: center;
}

.sortArrow button {
  width: 1em;
  text-decoration: none;
  color: white;
}

.boxart {
  position: absolute;
  top: 7%;
  right: 37.5%;
  width: 10%;
}

.liveIndicator {
  position: absolute;
  top: 25px;
  right: 25px;
  background: red;
  color: white;
  border-radius: 3px;
  padding: 0px 3px 0px 3px;
}